var dom = require('component-dom');

var _ = {

	// 
	tpl: require('./tpl').tpl,

	/**
	 * HTML
	 * @private
	 * @param  {string} name 
	 * @param  {object} data 
	 * @return {string}      HTML
	 */
	loadTpl: function (name, data){
		var tpl = require('../template/'+name+'.tpl');
		var html = this.tpl(tpl, data);
		// base style
		require('../template/'+name+'.less');
		// animate style
		require('../template/animate.less');
		return html;
	},

	/**
	 * HTMLDocument
	 * @private
	 * @param  {string} html HTML
	 * @return {object}      this
	 */
	appendHTML: function (html){
		this.el.body.append(html);
		return this;
	},

	// a empty function
	fun: function(){},

	// bind event
	addEvent: function(object, type, callback) {
	  if (object == null || typeof(object) == 'undefined') return;
	  if (object.addEventListener) {
	      object.addEventListener(type, callback, false);
	  } else if (object.attachEvent) {
	      object.attachEvent("on" + type, callback);
	  } else {
	      object["on"+type] = callback;
	  }
	},

	// unbind event
	removeEvent: function(object, type, callback) {
	  if (object == null || typeof(object) == 'undefined') return;
	  if (object.removeEventListener) {
	      object.removeEventListener(type, callback);
	  } else if (object.detachEvent) {
	      object.detachEvent("on" + type, callback);
	  } else {
	      object["on"+type] = null;
	  }
	},

	/**
	 * DOM traversal, manipulation and events aggregate library (like jQuery)
	 * https://github.com/component/dom
	 * @private
	 * @type {[type]}
	 */
	$ : dom

};

// DOM
_.el = {
	body: dom('body'),
	html: dom('html'),
	window: window
};

//  helper
module.exports = _;



